import React, { useState } from 'react'
import { Tabs, Form, Row, Checkbox } from 'antd'
import { Link } from 'react-router-dom'
import { UserOutlined, LockTwoTone, MailOutlined, AlipayCircleFilled, TaobaoCircleFilled, WeiboCircleFilled } from '@ant-design/icons'
import InputItem from '../../components/InputItem'
import SubmitButton from '../../components/SubmitButton'
import styles from './index.module.less'
import { useDispatch } from 'redux-react-hook'
import { login } from '../../actions/account'


const { TabPane } = Tabs;
const Login = () => {
    const [form] = Form.useForm();
    const [tabsSelectKey, setTabsSelectKey] = useState('1');
    const dispatch = useDispatch();
    const handleOnfish = (values) => {
        dispatch(login(values));
    }

    const handleTabsOnchange = (value) => {
        setTabsSelectKey(`${value}`)
    }

    return <div className={styles.loginContainer}>
        <div className={styles.login}>
            <Form
                form={form}
                onFinish={handleOnfish}
            >
                <Tabs defaultActiveKey='1' onChange={handleTabsOnchange}>
                    <TabPane tab='账号密码登录' key='1'>
                        <InputItem
                            name='username'
                            prefix={<UserOutlined style={{ color: '#1890ff' }} />}
                            placeholder='请输入用户名' size='large'
                            rules={[{ required: tabsSelectKey === '1', message: '请输入用户名' }]}
                        />
                        <InputItem
                            name='password'
                            type='password'
                            prefix={<LockTwoTone style={{ color: '#1890ff' }} />}
                            placeholder='请输入密码' size='large'
                            rules={[{ required: tabsSelectKey === '1', message: '请输入密码' }]}
                        />
                    </TabPane>
                    <TabPane tab='手机验证码登录' key='2'>
                        <InputItem
                            name='phone'
                            prefix={<UserOutlined style={{ color: '#1890ff' }} />}
                            placeholder='请输入手机号' size='large'
                            rules={[{ required: true, message: '请输入用户名' }]}
                        />
                        <InputItem
                            name='captcha'
                            prefix={<MailOutlined style={{ color: '#1890ff' }} />}
                            placeholder='请输入验证码' size='large'
                            rules={[{ required: true, message: '请输入密码' }]}
                        />
                    </TabPane>
                </Tabs>
                <div>
                    <Row justify='space-between' style={{ marginBottom: 24 }}>
                        <Checkbox size='large'>
                            自动登录
                    </Checkbox>
                        <a href='#!'>忘记密码</a>
                    </Row>
                    <SubmitButton >登录</SubmitButton>
                </div>
            </Form>
            <div>
                其他登录方式
                <AlipayCircleFilled className={styles.icons} />
                <TaobaoCircleFilled className={styles.icons} />
                <WeiboCircleFilled className={styles.icons} />
                <Link to='/register' className={styles.link}>注册账户</Link>
            </div>
        </div>
    </div>
};

export default Login;